@import '../../component/moment.module.scss';
@import '../../ellipsis.module.scss';
*{
    text-align: left;
    padding: 0;
    margin: 0;
}
.body{
    display: flex;
    &_body{
    padding: 40px 50px;
    flex: 1;
    }
}
.menus{
    position: relative;
}
.menuIcon{
    position: absolute;
    right: -45px;
    bottom: -56px;
    font-size: 30px;
    color: $color;
    padding-bottom: 10px;
    cursor: pointer;
    &:hover{
        color: darkorange;
    }
}
.body_menu{
    padding: 40px 10px;
    width: 150px;
    background-color: $color;
    opacity: .6;
    >div{
        margin-bottom: 20px;
        color: #fff;
        a{
            display: flex;
            align-items: center;
            color: #fff;
        }
        .icon{
            margin-left: auto;
            color: #fff;
        }
    }
}
.list{
    padding: 20px 0;
    border-bottom: 1px dashed #ccc;;
    &_h3{
        font-weight: bold;
    }
    &_div1{
        font-weight: 500;
    }
    &_div2{
        font-weight: 400;
    }
    &_div3{
        font-weight: 500;
        color: crimson;
    }
}
.pad{
    padding-left: 50px;
}
.menuItem{
@include ellipsis();
margin: 0;
}
.trueVisibe{
    width: 700px;
}
.falseVisible{
    width: 50px;
}