@mixin ellipsis {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
@mixin Line($height){
  height: $height;
  line-height: $height;
}
@mixin Font($size,$color,$weight){
  font-size: $size;
  color: $color;
  font-weight: $weight;
}
// 给table使用的
@mixin tbody{
  tr{
    td{
      padding-top: 0;
      padding-bottom: 0;                  
      @include Line(49px);
      @include ellipsis;
      &:nth-child(1){
        @include Font(14px, #111, 600);
      }
    }
    &:nth-child(1){
      @include Line(50px)
    }
  }
}
@mixin thead{
  tr{
    th{
      @include Line(36px);
      padding-top: 0;
      padding-bottom: 0;
    }
  }
}